export function createStep(httpClient: HttpClient): CreateStepSignature {
  return (step: Step, options?: CreateStepOptions) =>
    universalCreateStep(
      step,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateStepSignature {
  /**
   * Creates a Step.
   * @param - Step to be created.
   * @returns The created Step.
   */
  (step: Step, options?: CreateStepOptions): Promise<
    NonNullablePaths<
      Step,
      | `videoOptions.wixVideo._id`
      | `videoOptions.wixVideo.paid`
      | `videoOptions.requiredCompletionPercentage`
      | `videoOptions.deleted`
      | `quizOptions._id`
      | `quizOptions.totalQuestions`
      | `sectionId`
      | `description.title`
      | `ordering`
      | `stepType`
      | `programId`,
      4
    >
  >;
}

